// =============================================================================
export function formFieldsInit(options = { viewPass: false }) {
    const formFields = document.querySelectorAll('input[placeholder],textarea[placeholder]');
    if (formFields.length) {
        formFields.forEach((formField) => {
            if (!formField.hasAttribute('data-placeholder-nohide')) {
                formField.dataset.placeholder = formField.placeholder;
            }
        });
    }
    document.body.addEventListener('focusin', function (e) {
        const targetElement = e.target;
        if (targetElement.tagName === 'INPUT' || targetElement.tagName === 'TEXTAREA') {
            if (targetElement.dataset.placeholder) {
                targetElement.placeholder = '';
            }
            if (!targetElement.hasAttribute('data-no-focus-classes')) {
                targetElement.classList.add('_form-focus');
                targetElement.parentElement.classList.add('_form-focus');
            }
            formValidate.removeError(targetElement);
        }
    });
    document.body.addEventListener('focusout', function (e) {
        const targetElement = e.target;
        if (targetElement.tagName === 'INPUT' || targetElement.tagName === 'TEXTAREA') {
            if (targetElement.dataset.placeholder) {
                targetElement.placeholder = targetElement.dataset.placeholder;
            }
            if (!targetElement.hasAttribute('data-no-focus-classes')) {
                targetElement.classList.remove('_form-focus');
                targetElement.parentElement.classList.remove('_form-focus');
            }
            if (targetElement.hasAttribute('data-validate')) {
                formValidate.validateInput(targetElement);
            }
            if (targetElement.value.length && !targetElement.classList.contains('_form-error')) {
                targetElement.classList.add('_filled');
            } else {
                targetElement.classList.remove('_filled');
            }
        }
    });

    if (options.viewPass) {
        document.addEventListener('click', function (e) {
            let targetElement = e.target;
            if (targetElement.closest('[class*="__viewpass"]')) {
                let inputType = targetElement.classList.contains('_viewpass-active') ? 'password' : 'text';
                targetElement.parentElement.querySelector('input').setAttribute('type', inputType);
                targetElement.classList.toggle('_viewpass-active');
            }
        });
    }
}

// validation
export let formValidate = {
    getErrors(form) {
        let error = 0;
        let formRequiredItems = form.querySelectorAll('*[data-required]');
        if (formRequiredItems.length) {
            formRequiredItems.forEach((formRequiredItem) => {
                if ((formRequiredItem.offsetParent !== null || formRequiredItem.tagName === 'SELECT') && !formRequiredItem.disabled) {
                    error += this.validateInput(formRequiredItem);
                }
            });
        }
        return error;
    },
    validateInput(formRequiredItem) {
        let error = 0;
        if (formRequiredItem.dataset.required === 'email') {
            formRequiredItem.value = formRequiredItem.value.replace(' ', '');
            if (this.emailTest(formRequiredItem)) {
                this.addError(formRequiredItem);
                error++;
            } else {
                this.removeError(formRequiredItem);
            }
        } else if (formRequiredItem.dataset.required === 'tel') {
            // formRequiredItem.value = formRequiredItem.value.replace(/[^0-9]/g, ''); //      +()
            if (!/^\+\d{1,2} \(\d{3}\) \d{3} \d{2} \d{2}$/.test(formRequiredItem.value)) {
                this.addError(formRequiredItem);
                error++;
            } else {
                this.removeError(formRequiredItem);
            }
        } else if (formRequiredItem.dataset.required === 'num') {
            formRequiredItem.value = formRequiredItem.value.replace(/[^0-9]/g, ''); //      +()
            if (!formRequiredItem.value.trim()) {
                this.addError(formRequiredItem);
                error++;
            } else {
                this.removeError(formRequiredItem);
            }
        } else {
            if (!formRequiredItem.value.trim()) {
                this.addError(formRequiredItem);
                error++;
            } else {
                this.removeError(formRequiredItem);
            }
        }
        return error;
    },
    addError(formRequiredItem) {
        formRequiredItem.classList.add('_form-error');
        formRequiredItem.parentElement.classList.add('_form-error');
        let inputError = formRequiredItem.parentElement.querySelector('.form__error');
        if (inputError) formRequiredItem.parentElement.removeChild(inputError);
        if (formRequiredItem.dataset.error) {
            formRequiredItem.parentElement.insertAdjacentHTML('beforeend', `<div class="form__error">${formRequiredItem.dataset.error}</div>`);
        }
    },
    removeError(formRequiredItem) {
        formRequiredItem.classList.remove('_form-error');
        formRequiredItem.parentElement.classList.remove('_form-error');
        if (formRequiredItem.parentElement.querySelector('.form__error')) {
            formRequiredItem.parentElement.removeChild(formRequiredItem.parentElement.querySelector('.form__error'));
        }
    },
    formClean(form) {
        form.reset();
        setTimeout(() => {
            let inputs = form.querySelectorAll('input,textarea');
            for (let index = 0; index < inputs.length; index++) {
                const el = inputs[index];
                el.parentElement.classList.remove('_form-focus');
                el.classList.remove('_form-focus');
                formValidate.removeError(el);
            }
            let checkboxes = form.querySelectorAll('.checkbox__input');
            if (checkboxes.length > 0) {
                for (let index = 0; index < checkboxes.length; index++) {
                    const checkbox = checkboxes[index];
                    checkbox.checked = false;
                }
            }
            if (modules.select) {
                let selects = form.querySelectorAll('.select');
                if (selects.length) {
                    for (let index = 0; index < selects.length; index++) {
                        const select = selects[index].querySelector('select');
                        modules.select.selectBuild(select);
                    }
                }
            }
        }, 0);
    },
    emailTest(formRequiredItem) {
        return !/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,8})+$/.test(formRequiredItem.value);
    },
};

// form submition
export function formSubmit(options = { validate: true }) {
    const forms = document.forms;
    if (forms.length) {
        for (const form of forms) {
            form.addEventListener('submit', function (e) {
                const form = e.target;
                formSubmitAction(form, e);
            });
            form.addEventListener('reset', function (e) {
                const form = e.target;
                formValidate.formClean(form);
            });
        }
    }
    async function formSubmitAction(form, e) {
        const error = !form.hasAttribute('data-no-validate') ? formValidate.getErrors(form) : 0;
        if (error === 0) {
            const ajax = form.hasAttribute('data-ajax');
            if (ajax) {
                e.preventDefault();
                const formAction = form.getAttribute('action') ? form.getAttribute('action').trim() : '#';
                const formMethod = form.getAttribute('method') ? form.getAttribute('method').trim() : 'GET';
                const formData = new FormData(form);

                form.classList.add('_sending');
                const response = await fetch(formAction, {
                    method: formMethod,
                    body: formData,
                });
                if (response.ok) {
                    let responseResult = await response.json();
                    form.classList.remove('_sending');
                    formSent(form, responseResult);
                } else {
                    alert('error');
                    form.classList.remove('_sending');
                }
            } else if (form.hasAttribute('data-dev')) {
                // in development mode
                e.preventDefault();
                formSent(form);

                if (form.classList.contains('text-change__form')) {
                    const success = form.parentElement.querySelector('.text-change__form-success');
                    success.style.display = 'block';
                    form.style.display = 'none';
                }
            }
        } else {
            e.preventDefault();
            const formError = form.querySelector('._form-error');
            if (formError && form.hasAttribute('data-goto-error')) {
                gotoBlock(formError, true, 1000);
            }
        }
    }
    // actions after submitting the form
    function formSent(form, responseResult = ``) {
        // form submit event
        document.dispatchEvent(
            new CustomEvent('formSent', {
                detail: {
                    form: form,
                },
            })
        );
        // show popup, if popup module is connected and form has setting
        setTimeout(() => {
            if (modules.popup) {
                const popup = form.dataset.popupMessage;
                popup ? modules.popup.open(popup) : null;
            }
        }, 500);
        // clean form
        // formValidate.formClean(form);
        // console output
        // formLogging(`form sent`);
    }
}

// quantity
export function formQuantity() {
    document.addEventListener('click', function (e) {
        let targetElement = e.target;
        if (targetElement.closest('.quantity__button')) {
            let value = parseInt(targetElement.closest('.quantity').querySelector('input').value);
            if (targetElement.classList.contains('quantity__button_plus')) {
                value++;
            } else {
                --value;
                if (value < 1) value = 1;
            }
            targetElement.closest('.quantity').querySelector('input').value = value;
        }
    });
}

// rating
export function formRating() {
    const ratings = document.querySelectorAll('.rating');
    if (ratings.length > 0) {
        initRatings();
    }
    // main function
    function initRatings() {
        let ratingActive, ratingValue;
        // "run" through all the ratings on the page
        for (let index = 0; index < ratings.length; index++) {
            const rating = ratings[index];
            initRating(rating);
        }
        // initialize a specific rating
        function initRating(rating) {
            initRatingVars(rating);

            setRatingActiveWidth();

            if (rating.classList.contains('rating_set')) {
                setRating(rating);
            }
        }
        // variables initialization
        function initRatingVars(rating) {
            ratingActive = rating.querySelector('.rating__active');
            ratingValue = rating.querySelector('.rating__value');
        }
        // change the width of active stars
        function setRatingActiveWidth(index = ratingValue.innerHTML) {
            const ratingActiveWidth = index / 0.05;
            ratingActive.style.width = `${ratingActiveWidth}%`;
        }
        // indicate the grade
        function setRating(rating) {
            const ratingItems = rating.querySelectorAll('.rating__item');
            for (let index = 0; index < ratingItems.length; index++) {
                const ratingItem = ratingItems[index];
                ratingItem.addEventListener('mouseenter', function (e) {
                    // update variables
                    initRatingVars(rating);
                    // update active stars
                    setRatingActiveWidth(ratingItem.value);
                });
                ratingItem.addEventListener('mouseleave', function (e) {
                    // update active stars
                    setRatingActiveWidth();
                });
                ratingItem.addEventListener('click', function (e) {
                    // update variables
                    initRatingVars(rating);

                    if (rating.dataset.ajax) {
                        // "send" to server
                        setRatingValue(ratingItem.value, rating);
                    } else {
                        // show specified grade
                        ratingValue.innerHTML = index + 1;
                        setRatingActiveWidth();
                    }
                });
            }
        }
        async function setRatingValue(value, rating) {
            if (!rating.classList.contains('rating_sending')) {
                rating.classList.add('rating_sending');

                // sending data (value) to server
                let response = await fetch('rating.json', {
                    method: 'GET',

                    //body: JSON.stringify({
                    //	userRating: value
                    //}),
                    //headers: {
                    //	'content-type': 'application/json'
                    //}
                });
                if (response.ok) {
                    const result = await response.json();

                    // get new rating
                    const newRating = result.newRating;

                    // new average result output
                    ratingValue.innerHTML = newRating;

                    // update active stars
                    setRatingActiveWidth();

                    rating.classList.remove('rating_sending');
                } else {
                    alert('error');

                    rating.classList.remove('rating_sending');
                }
            }
        }
    }
}
